<template>
  <div class="songs-list">
    <div class="back" @click.stop="$router.go(-1)">&nbsp;&lt; 返回</div>
    <div> 
      <img class="tou" :src="songimg" alt="" />
      <span>{{ gedanName }}</span>
      <div class="mask">
        <img class="bac" :src="songimg" alt="" />
      </div>
    </div>
    <h3 class="gd-list">歌单列表</h3>
    <music-list v-if="songlist.length > 0" :musiclist="songlist" :shownum="shownum" @play="$emit('play',$event)"></music-list>
    <load-animate v-else></load-animate>
    <h3 class="pl-list">精彩评论</h3>

    <div class="jcpl-box" v-for="(pl, index) in plData.hotComments" :key="index">
      <div class="top-img">
        <img class="auto-img" :src="pl.user.avatarUrl" alt="" />
      </div>
      <div class="pl-top">
        <div class="top-nt">
          <div class="top-box">
            <div class="top-name">{{ pl.user.nickname }}</div>
            <div class="top-date">{{ pl.timeStr }}</div>
          </div>
            <div class="top-dz">
                <div class="dz-box" >
                    <img ref="dzimg" @click="gitDz(pl,index)" class="auto-img"  src="../assets/dianzan1.png" alt="">
                </div>
              {{ pl.likedCount }}
            </div>
        </div>

        <div class="pl-font">
          <div class="font-text">{{ pl.content }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MusicList from "@/components/MusicList.vue";
import LoadAnimate from "@/components/LoadAnimate.vue";
// import ImagesItem from '@/components/ImagesItem.vue'
export default {
  props: {
    listid: {
      type:String,
      default: "0",
    },
  },
  data() {
    return {
      songlist: [],
      songimg: "",
      shownum: true,
      isImg:false,
      gedanName: "",
      plData: [],
    };
  },
  components: {
    MusicList,
    LoadAnimate,
  },
  created() {
    this.getSongList();
    this.getPl();
  },
  methods: {

      gitDz(e,i){
          if(this.isImg){
              this.$refs.dzimg[i].src = require('../assets/dianzan1.png');
              e.likedCount = e.likedCount - 1;
              this.isImg = false;
          }else{
              this.$refs.dzimg[i].src = require('../assets/dianzan2.png');
              e.likedCount = e.likedCount + 1;
              this.isImg = true;
          }
      },

    getPl() {
      this.axios({
        method: "get",
        url: `/comment/playlist?id=${this.listid}`,
      })
        .then((result) => {
          this.plData = result;
          console.log("this.pldata=>", this.plData.hotComments);
        })
        .catch((err) => {
          console.log(err);
        });
    },

    async getSongList() {
      await this.axios.get(`/playlist/detail?id=${this.listid}`).then((re) => {
        console.log(re);
        this.gedanName = re.playlist.name;
        this.songimg = re.playlist.coverImgUrl;
        console.log("this.songlistimg==>", this.songlistimg);
        this.songlist = re.playlist.tracks.map((item) => {
          item.song = {
            alias: item.alia,
            //  privilege:re.privilege[index],
            artists: item.ar,
            album: item.al,
          };
          return item;
        });
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.jcpl-box {
  padding: 10px;
  display: flex;
  // align-items: center;
  .pl-top {
    flex: 2;
    .top-nt {
        display: flex;
      .top-name {
        padding: 0 0 10px 0;
        font-size: 12px;
       color: #666666;
      }
      .top-dz {
        font-size: 12px;
        margin-left: auto;
        display: flex;
        align-items: center;
        .dz-box{
            width: 20px;
            height: 20px;
            margin-right: 8px;
            .auto-img{
                width: 100%;
                display: block;
            }
        }
      }
      .top-date {
        font-size: 12px;
        color: #666;
      }
    }
  }
  .top-img {
    width: 30px;
    height: 30px;
    margin-right: 10px;
    .auto-img {
      border-radius: 50%;
      height: 100%;
      display: block;
    }
  }
  .pl-font {
    font-size: 14px;
    padding: 10px 0;
  }
}

.songs-list {
  position: fixed;
  z-index: 9997;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  overflow: scroll;
  .back{
    z-index: 222;
   color: black;
    font-size: 18px;
    height: 20px;
    padding: 2px 0;
  }
  div {
    position: relative;
    .tou {
      width: 33%;
      position: absolute;
      top: 30px;
      left: 20px;
      z-index: 3333;
    }
    span {
      width: 209px;
      height: 45px;
      display: inline-block;
      position: absolute;
      top: 40px;
      right: 10px;
      color: black;
      font-size: 17px;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
    .mask {
      width: 100%;
      height: 200px;
      background-color: rgba(0,0,0,.25);
      opacity: 0.2;
      z-index: 2;
      .bac {
        width: 33%;
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }
  .gd-list {
    height: 23px;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
  }
  .pl-list {
    height: 23px;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
  }
}
</style>
